import React, { memo, useEffect } from 'react'
import { Link, NavLink } from 'react-router-dom';
import styles from './Header.module.scss';
import classNames from 'classnames';


function Header() {

  // useEffect(() => {
  //   (async () => {

  //     const data = await fetch('https://catfact.ninja/fact').then(res => res.json());

  //     console.log(data)

  //   })()
  // }, []);
  return (
    <header>
      <nav>
        <ul>
          <li>
            <NavLink className={({ isActive }) => `${styles.link} ${isActive ? styles.active : ''}`} to='/'>Home</NavLink>
          </li>

          <li>
            <NavLink className={({ isActive }) => `${styles.link} ${isActive ? styles.active : ''}`} to='/blog'>blog</NavLink>
          </li>

          <li>
            <NavLink className={({ isActive }) => classNames(styles.link, { [styles.active]: isActive })} to='/posts'>posts</NavLink>
          </li>
        </ul>
      </nav>
     </header>
  )
}

export default memo(Header);