import React, { lazy, Suspense } from 'react';
import { Routes, Route } from 'react-router-dom';
import HomePage from './pages/Home';
// import BlogPage from './pages/Blog';
// import OneBlogPage from './pages/OneBlogPage';

const BlogPage = lazy(() => import('./pages/Blog'));
const OneBlogPage = lazy(() => import('./pages/OneBlogPage'));

const AppRoutes = ({ title }) => {

    return (
      <Suspense fallback={<h2>Loading....</h2>}>
        <Routes>
            <Route path='/' element={<HomePage />} />
            <Route path='/blog' element={<BlogPage title={title} />} />
            <Route path='/blog/:id' element={<OneBlogPage title={title} />} />
        </Routes>
      </Suspense>
    )
}

export default AppRoutes;